*{ margin: 0; padding: 0; -webkit-box-sizing: border-box; box-sizing: border-box; list-style-type: none; }

body {
  font-family: 'Muli', Helvetica, Arial;
  font-size: 16px;
}

.app {
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.main-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 80px;
  padding: 2em 5em;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: transparent;
  -webkit-box-shadow: 0 6px 15px rgba(36,37,38,0.08);
          box-shadow: 0 6px 15px rgba(36,37,38,0.08);
}

.main-header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-filter: blur(40px);
          filter: blur(40px);
  z-index: -1;
}

.color-list {
  width: 80%;
  height: 100%;
  margin: 5em auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.color-list__item {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  width: 160px;
  height: 180px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #fff;
  -webkit-box-shadow: 0 6px 15px rgba(36,37,38,0.08);
          box-shadow: 0 6px 15px rgba(36,37,38,0.08);
  border-collapse: collapse;
  border-radius: 16px;
  cursor: pointer;
  margin: 1.5em;
}

.color-list__item:not(:last-of-type) {
  margin-right: 1.5em;
}

.color-display {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
  border-radius: 16px 16px 0 0;
}

.color-data {
  padding: 1em;
  font-size: .90rem;
}

.color {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.color:not(:last-of-type) {
  margin-bottom: .50em;
}

.color-box {
  width: 10px;
  height: 10px;
  margin-right: 1em;
  border-radius: 50%;
}

@media (max-width: 600px) {
  .color-list__item {
    width: 100%;
    height: 300px;
    margin: 2em 0;
  }
}


.tracking-in-expand {
	-webkit-animation: tracking-in-expand 0.5s cubic-bezier(0.785, 0.135, 0.150, 0.860) both;
	        animation: tracking-in-expand 0.5s cubic-bezier(0.785, 0.135, 0.150, 0.860) both;
}

 @-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}



